@import url('https://fonts.googleapis.com/css?family=Allan');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@500&display=swap');

$dark: lighten(#121214, 5%);
$steps: 12;
$primary: #0087ff;
$controller_size: 400px;
$radius: 4px;

$body: rgb(255, 237, 211);

// $base: rgb(169, 169, 62);
$base: rgba(255, 237, 211, 0.146);
$shadow: rgba(255, 255, 255, 0.92);

$button-txt: rgb(255, 237, 211);
$button-hover: rgb(85, 85, 10);
$button-bg: rgb(125, 125, 29);

$notes-txt: rgb(85, 85, 10);
$notes-bg: rgb(255, 237, 211);

$pad: rgba(255, 237, 211, 1);
$pad-transparent: rgba(255, 237, 211, 0);


