body {
  // background: $dark;
  background: $body;
  font-family: 'Roboto Mono', monospace;
  font-weight: 700;
  display: flex;
  flex-direction: column;
  height: 100vh; 
  padding-top: 1em;
  text-align: center;
  padding: none;
  align-content: center;
}

.flex {
  display: flex;
  flex-direction: row;
}

.board {
  padding-top: 0;
  width: 100%;
  height: 75vh;
  margin: 100px 0 60px 0;

  background-image: url('../img/board-r.png');
  background-position: center;
  background-repeat: no-repeat;
  background-size: 1200px;
  align-content: center;
}

.Sequencer {
  margin: -50px 0px 0px -15px;
  position: relative;
  display: flex;
  border-radius: $radius;
  background-color: transparent;
  width: 100%;
  // background-color: red;
  justify-content: center;
}

.seq-wrapper {
  display: flex;
  justify-content: center;
  margin: 55px 300px 0 0;
  // margin-right: 300px;
}

.notes {
  list-style-type: none;
  // display: inline-block;
  // position: absolute;
  // left: -50px;
  top: -20px;
  text-align: center;
  color: $notes-bg;
  line-height: 46px;
  // opacity: 0.5;
  padding-right: 20px;
  border-radius: 15px;
  // background-color: $notes-bg;
  align-content: center;

}


.pad-wrapper {
  // display: inline-block;
  margin: 0 auto;
}

.pads {
  box-sizing: border-radius;
  display: flex;
  flex-direction: column-reverse;
  flex-wrap: wrap;

  @for $i from 0 through ($steps - 1) {
    #pad-group-#{$i}.on, #pad-group-#{$i}:hover{
      transition: opacity 0.5s ease;
      opacity: 1;
      background-image: url('../img/item-'+#{$i}+'.png');
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center;

    }
    #pad-group-#{$i}.active {
      #pad-group-#{$i}:not(.on) {
        background: radial-gradient(circle, $pad 0%, rgba(255,196,213,1) 25%, $pad-transparent 70%);
      }
      #pad-group-#{$i}.on {
        background-image: url('../img/item-'+#{$i}+'.png');
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
      }

      opacity: 1;
      // background-image: url('../img/item-'+#{$i}+'.png');
      // background-size: contain;
      // background-repeat: no-repeat;
      // background-position: center;
    }
    #pad-group-#{$i}:not(.active) {
      #pad-group-#{$i}:not(.on) {
        opacity: 0;
      }
      #pad-group-#{$i}.on {
        opacity: 1;
      }
      
      // transition: opacity 0.25s ease;
      background-image: url('../img/item-'+#{$i}+'.png');
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center;
    }
  }

  
  .pad {
    
    // background: lighten($dark, 10%);
    // border-radius: $radius;
    cursor: pointer;
    width: calc(#{$controller_size} / 10);
    height: calc(#{$controller_size} / 10);
    margin: 4px;
    // box-shadow: 0 0px 3px 0 $shadow;
    // transition: background 0.5s ease;
    // background-position: center;
    // background-size: contain;
    // background-repeat: no-repeat;
    background-color: transparent;
    opacity: 0;


    &:hover {
      // background: lighten($dark, 15%);
      // background: transparent;
      // background: radial-gradient(circle, $pad 0%, rgba(255,196,213,1) 25%, $pad-transparent 70%);
      
      // &.on {
      //   // background-color: $primary!important;
      //   background-color: transparent;

      //   // box-shadow: none;
      //   // box-shadow: 0 0 12px 0 rgba($primary, 0.8);
      // }
      // transition: opacity 3s ease;
      // background-image: url('../img/item-'+#{$i}+'.png');
      // background-size: contain;
      // background-repeat: no-repeat;
      // background-position: center;


      not &.on {
        // background-size: 0px 0px;
        // background: transparent;
        background: radial-gradient(circle, $pad 0%, rgba(255,196,213,1) 25%, $pad-transparent 70%);
        
      }
    
    }
    &.active {
      opacity: 1;
      // background: lighten($dark, 15%);
      background: radial-gradient(circle, rgba(255,237,211,1) 0%, rgba(255,196,213,1) 25%, rgba(255,237,211,0) 70%);
    
    }

    // &.on {
    //   // background-color: $primary!important;
    //   background-color: transparent;

    //   box-shadow: none;
    //   // box-shadow: 0 0 12px 0 rgba($primary, 0.8);
    // }

  }
  
}

.button-wrapper {
  margin: 0 auto;
}

.buttons {
  display: flex;
  padding: 1em 0em 0em 10em;
  // margin: 3em auto;
  margin-left: -450px;
  margin-top: -50px;
  justify-content: center;
  // width: 100%;
  // display: inline-block;
    
  button, select, input {
    font-family: 'Roboto Mono', monospace;
    display: inline-block;
    margin: 5px;
    outline: none;
    // background: $button-bg;
    background-color: transparent;
    border: 4px solid $button-bg;
    color: $button-bg;
    padding: 10px;
    font-size: 14px;
    letter-spacing: 1px;
    // border-radius: 4px;
    // box-shadow: 0 1px 2px 0 rgba(black, 0.2);
    cursor: pointer;

    &:hover {
      // background: lighten($dark, 15%);
      background-color: $button-hover;
    }

    &.active {
      color: $primary;
      border: 2px solid $primary;
    }
  }

  .select-wrapper {
    position: relative;
    display: inline-block;

    span {
      position: absolute;
      color: $button-hover;
      top: -20px;
      left: 8px;
      font-size: 12px;
      letter-spacing: 1px;
      opacity: 0.7;
    }
  }

  // select {
  //   position: relative;
  //   height: 42px;
  //   min-width: 50px;
  // }
}



@keyframes colorize {
  from { background: hsla(0, 100%, 50%, 1) }
  to   { background: hsla(100, 100%, 50%, 1) }
}


footer {
  margin-top: 2em;
  color: rgba(white, 0.6);
  font-weight: 300;
  letter-spacing: 0.5px;
  text-align: center;

  a {
    color: white;
    font-weight: 400;
    border-bottom: 1px dotted;
    text-decoration: none;

    &:hover {
      border-bottom: 1px solid;
    }
  }
}


.user-info {
  text-align: right;
  margin: 5%;
}

.email {
  color: $button-bg;
}